import { RouterView } from "vue-router";
const Dashboard = () =>
    import ("@/views/Dashboard.vue");

const DashboardViews = {
    path: "dashboard",
    name: "Dashboard",
    component: RouterView,
    children: [{
            path: "/dashboard",
            name: "Dashboard",
            component: Dashboard,
        },

    ],
};

export default DashboardViews;